import React, { Suspense } from 'react';
import { Routes, Route } from 'react-router-dom';
import { ROUTES } from './constants/routes';

import './styles/globals.scss';

import AppLayout from './components/layout/AppLayout';
import ErrorBoundary from './components/ErrorBoundary/ErrorBoundary';
import GenericErrorPage from './components/ErrorBoundary/GenericErrorPage';
import Spinner from './components/Spinner/Spinner';

const AboutPage = React.lazy(() => import('./pages/AboutPage/AboutPage'));
const HomePage = React.lazy(() => import('./pages/HomePage/HomePage'));
const TrainPage = React.lazy(() => import('./pages/TrainPage/TrainPage'));
const AddWordPage = React.lazy(() => import('./pages/AddWordPage/AddWordPage'));
const VocabPage = React.lazy(() => import('./pages/VocabPage/VocabPage'));
const NotFoundPage = React.lazy(() => import('./pages/NotFoundPage/NotFoundPage'));

function App() {
  return (
    <AppLayout>
      <Suspense fallback={<Spinner />}>
        <Routes>
          <Route path={ROUTES.ABOUT} element={<AboutPage />} />

          <Route
            path={ROUTES.HOME}
            element={
              <ErrorBoundary
                FallbackComponent={(props) => (
                  <GenericErrorPage
                    title="   "
                    message="     ."
                    linkPath="/home"
                    buttonText=""
                    errorInfo={props.errorInfo}
                  />
                )}
              >
                <HomePage />
              </ErrorBoundary>
            }
          />

          <Route
            path={ROUTES.TRAIN}
            element={
              <ErrorBoundary
                FallbackComponent={(props) => (
                  <GenericErrorPage
                    title="   "
                    message="   TrainPage. ,   ."
                    linkPath="/train"
                    buttonText=" "
                    errorInfo={props.errorInfo}
                  />
                )}
              >
                <TrainPage />
              </ErrorBoundary>
            }
          />

          <Route
            path={ROUTES.ADD_WORD}
            element={
              <ErrorBoundary
                FallbackComponent={(props) => (
                  <GenericErrorPage
                    title="     "
                    message="      AddWordPage."
                    linkPath="/add"
                    buttonText=" "
                    errorInfo={props.errorInfo}
                  />
                )}
              >
                <AddWordPage />
              </ErrorBoundary>
            }
          />
    
          <Route
            path={ROUTES.VOCAB}
            element={
              <ErrorBoundary
                FallbackComponent={(props) => (
                  <GenericErrorPage
                    title="     "
                    message="VocabPage   .   ."
                    linkPath="/vocab"
                    buttonText=" "
                    errorInfo={props.errorInfo}
                  />
                )}
              >
                <VocabPage />
              </ErrorBoundary>
            }
          />

          <Route path={ROUTES.NOT_FOUND} element={<NotFoundPage />} />
        </Routes>
      </Suspense>
    </AppLayout>
  );
}

export default App;
